.container {
  width: 80em;
  margin: auto;
  background-color: cadetblue;
  min-height: 100vh;
  height: 100%;
  display: flex;
  flex-direction: row;
}

.heading {
  width: 100%;
  font-size: 20px;
  font-weight: 600;
  display: flex;
  justify-content: center;
  height: 4rem;
  align-items: center;
  border-bottom: solid thin #ccc;
  background-color: rgb(214, 214, 179);
}

.people {
  width: 30%;
  background-color: beige;
}

.chatBox {
  width: 70%;
}

.window {
  margin: 0.6rem 1rem;
}

.chatTitle {
  width: 100%;
  font-weight: 600;
  font-size: 18px;
  color: azure;
  height: 3rem;
  display: flex;
  align-items: center;
  padding: 0 1rem 0 1rem;
  border-bottom: solid thin white;
}

.message {
  max-width: max-content;
  border-radius: 2px;
  background-color: white;
  padding: 0.7rem 1rem;
  font-weight: 600;
  margin-bottom: 1rem;
}
